import { Component, OnInit, ViewChild, ViewChildren, Input, OnDestroy } from '@angular/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';

import { SharedAnimations } from '../../../../shared/animations/shared-animations';
import { Router, ActivatedRoute } from '@angular/router';
import { AuditService, Audit } from '../../../../shared/services/audit.service';
import { AttachmentService } from '../../../../shared/services/attachment.service';
import { saveAs } from 'file-saver';
import { AuditResponse, AuditEvent } from '../../../../shared/models';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TransporterService } from '../../../../shared/services/transporter.service';
import { Transporter } from '../../../../shared/models/transporter';

@Component({
  selector: 'transporter-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss'],
  animations: [SharedAnimations]
})
export class ContentComponent implements OnInit {

  activeTransporter: Transporter = new Transporter();

  loadAuditDataSub: Subscription;
  transporterSelectSub: Subscription;
  transporterId: number;
  loadingData = false;


  showHistory = true;
  audits: AuditEvent[];
  auditEventDetail: string[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public service: TransporterService,
    private auditService: AuditService,
    private router: Router,
    private modalService: NgbModal,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.route.params.subscribe(params => this.transporterId = params['id']);
    this.transporterSelectSub = this.service.onTransporterSelected.subscribe(res => {
      if (res) {
        console.log("service.loadingData", this.service.loadingData)
        this.activeTransporter = res.transporter;
        this.showHistory = false;
      }
    });
  }

  ngOnDestroy() {
    if (this.transporterSelectSub) { this.transporterSelectSub.unsubscribe(); }
  }

  

  edit(transporter) {
    this.router.navigate(['edit', transporter.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.getAuditTrails('transporter', this.activeTransporter.id)
        .subscribe(res => {
          this.audits = res.events;
        });
    }
  }

  clone(transporter: Transporter) {
    this.router.navigate(['clone', transporter.id], {relativeTo: this.route})
  }

  confirm(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title', centered: true })
    .result.then((result) => {
      console.log("model delete : " + result);
    }, (reason) => {
      console.log("model reason : " + reason);
    });
  }

  open(content, eventId) {
    this.auditService.getAuditEventDetail(eventId).subscribe(
      response => {
        this.auditEventDetail = response.changeDetails;
        this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
      .result.then((result) => {
        console.log(result);
      }, (reason) => {
        console.log('Err!', reason);
      });
      },
      error => {
        console.log(error);
      }
    )
    
  }

}
