import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { FinancialService } from '../../../shared/services/financial.service';
import { HttpParams } from '@angular/common/http';
import { TransporterService } from '../../../shared/services/transporter.service';

@Component({
  selector: 'app-transporter',
  templateUrl: './transporter.component.html',
  styleUrls: ['./transporter.component.scss']
})
export class TransporterComponent implements OnInit {

  public isCollapsed = true;

  advancedSearchForm: FormGroup;

  constructor(private fb: FormBuilder,
    private service: TransporterService
  ) { }

  ngOnInit(): void {
    this.setAdvancedSearchForm();
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      status: ''
    });
  }

  clear() {
    debugger;
    this.advancedSearchForm.controls['name'].setValue('');
    this.advancedSearchForm.controls['status'].setValue('');
    this.service.performAdvancedSearch();
  }

  performAdvancedSearch() {
    let name = this.advancedSearchForm.controls['name'].value;
    let status = this.advancedSearchForm.controls['status'].value;
    const httpOptions = {
      params: new HttpParams().set("name", name).set("status", status)
    };
    this.service.performAdvancedSearch(httpOptions);
  }

}
