import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subscription } from 'rxjs';
import { map, switchMap, startWith, debounceTime } from 'rxjs/operators';
import { Transporter } from '../../../../shared/models/transporter';
import { TransporterService } from '../../../../shared/services/transporter.service';
import { HttpParams } from '@angular/common/http';

@Component({
  selector: 'transporter-left-pane',
  templateUrl: './left-pane.component.html',
  styleUrls: ['./left-pane.component.scss']
})
export class LeftPaneComponent implements OnInit {

  advancedSearchSub: Subscription;

  transporters: Transporter[];
  loadDataSub: Subscription;
  selectedAction = 'All';
  loadingCollection = false;
  advancedSearchApplied = false;

  page = 1;
  pageSize = 6;
  collectionSize: number;
  
  searchControl: FormControl = new FormControl();

  filterAction: string[] = ['All', 'Active', 'Inactive'];

  advancedSearchForm: FormGroup;

  constructor(public service: TransporterService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private fb: FormBuilder) {
  }


  ngOnInit(): void {
    
    this.loadingCollection = true;
    this.setAdvancedSearchForm();

    this.loadDataSub = this.service.loadTransporterData(this.page, this.pageSize)
      .subscribe(res => {
        this.transporters = res.content;
        this.collectionSize = res.totalElements;
       this.loadingCollection = false;
    });

    this.searchControl.valueChanges
      .pipe(debounceTime(200))
      .subscribe(value => {
        this.filerData(value);
    });

    // Listen for company change
    this.advancedSearchSub = this.service.onAdvancedSearchApplied.subscribe(res => {
      if (res) {
        this.transporters = res.transporters.content;
        this.collectionSize = res.transporters.totalElements;
      }
    });

  }

  filerData(val) {
    let params = new HttpParams();
    params = params.set("name", val);
    if (this.selectedAction.toLowerCase() != 'all') {
      params = params.set("status", this.selectedAction.toUpperCase());
    }
    const httpOptions = {
      params: params
    };

    this.service.performAdvancedSearch(httpOptions);
  }

  filerDataWithStatus(val) {
    let name = this.searchControl.value;
    let params = new HttpParams();
    if (name && name !='') {
      params = params.set('name', name);
    }
    params = params.set('status', this.selectedAction.toUpperCase());
    const httpOptions = {
      params: params
    };
    this.service.performAdvancedSearch(httpOptions);
  }

  getTransporterByid(financialId) {
    this.service.getTransporterById(financialId)
      .subscribe(res => {
        console.log(res);
      }, err => {
        console.log(err);
      });
  }

  getPageFromService(page: number) {
    this.loadingCollection = true;
    this.service.loadTransporterData(page, this.pageSize)
      .subscribe(res => {
        this.transporters = res.content;
        this.collectionSize = res.totalElements;
       this.loadingCollection = false;
       this.page = page;
    });
  }

  filter(action) {
    this.selectedAction = action;
    this.filerDataWithStatus(action.toString());
  }

  add() {
    this.router.navigate(['add'], { relativeTo: this.route });
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      status: ''
    });
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
    .result.then((result) => {
      console.log(result);
    }, (reason) => {
      console.log('Err!', reason);
    });
  }

  performAdvancedSearch(event) {
    this.loadingCollection = true;
    this.modalService.dismissAll('close clicked');
    this.advancedSearchApplied = true;
  }

  clearSearchCriteria() {
    this.advancedSearchApplied = false;
    this.filerData('');
  }
}
